import React, { useState } from "react";

function ProgramDetails({ data, setOpensidebar }) {
  const [opendiv, setOpendiv] = useState(null);

  const closeSidebar = () => {
    setOpensidebar(false);
  };

  const openProg = (id) => {
    setOpendiv(id);
  };
  return (
    <div>
      <div className="infoSidebar">
        <div className="info_sidebar_container">
          <div>
            <img
              className="org_icon"
              onClick={closeSidebar}
              src={require("../src/icon/remove.svg").default}
            />
          </div>
          <div style={{ marginTop: "40px" }}>
            {data.length > 0
              ? data.map((dataik, index) => {
                return (
                  <div className="info_detail" key={dataik.id}>
                    <div key={index}>
                      <div
                        style={{
                          // marginTop: "40px",
                          // paddingRight: "17px",
                          cursor: "pointer",
                          fontSize: "18px",
                          fontWeight: "500",
                          display: "flex",
                          flexWrap: "wrap",
                          padding: "10px 0px 10px 24px",
                          color: "#808A8A",
                        }}
                        onClick={(e) => {
                          openProg(dataik.id);
                          // setIsOpen(true);
                        }}
                      >
                        <div style={{ width: "164px" }}>{dataik.name}</div>
                        {/* {isOpen ? ( */}
                        <img
                          className="arrow_select"
                          src={require("../src/icon/arrow.svg").default}
                        />
                        {/* ) : null} */}
                      </div>

                      {opendiv === dataik.id ? (
                        <div className="supports_types">
                          <div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={require("../src/icon/time.svg").default}
                              />
                              <p className="paragraph">
                                {dataik.start_date}-{dataik.end_date}
                              </p>
                            </div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/community.svg").default
                                }
                              />
                              <p className="paragraph">{dataik.community}</p>
                            </div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/budget.svg").default
                                }
                              />
                              <p className="paragraph">{dataik.budget}</p>
                            </div>

                            {dataik.category_support.map(
                              (datachok, index) => {
                                return (
                                  <div className="info_row" key={index}>
                                    <img
                                      className="det_icon"
                                      src={
                                        require("../src/icon/sphere.svg")
                                          .default
                                      }
                                    />
                                    <div>
                                      <p className="paragraph">
                                        {" "}
                                        {datachok.category}{" "}
                                      </p>
                                      <p className="paragraph">
                                        {datachok.support}
                                      </p>
                                    </div>
                                  </div>
                                );
                              }
                            )}
                            {dataik.organization.map((datachok, index) => {
                              return (
                                <div className="info_row" key={index}>
                                  <img
                                    className="det_icon"
                                    src={
                                      require("../src/icon/organization.svg")
                                        .default
                                    }
                                  />
                                  <p className="paragraph">{datachok}</p>
                                </div>
                              );
                            })}
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/head name.svg").default
                                }
                              />
                              <p className="paragraph">{dataik.manager}</p>
                            </div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/contact.svg").default
                                }
                              />
                              <p className="paragraph">
                                {dataik.contactPerson}
                              </p>
                            </div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/ongoing.svg").default
                                }
                              />
                              <p className="paragraph">{dataik.status}</p>
                            </div>
                            <div className="info_row">
                              <img
                                className="det_icon"
                                src={
                                  require("../src/icon/description.svg")
                                    .default
                                }
                              />
                              <p className="paragraph">
                                {dataik.description}
                              </p>
                            </div>
                          </div>
                        </div>
                      ) : null}
                    </div>
                  </div>
                );
              })
              : // <div>
              //   <p>Loading...</p>
              // </div>
              null}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProgramDetails;
